import {OrderDetailComponent} from './order-detail/order-detail.component';
import {UserAccountComponent} from './user-account/user-account.component';
import {UserComponent} from './user.component';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {OrderComponent} from './order/order.component';

const routes: Routes = [
	{
		path: '',
		component: UserComponent,
		children: [
			{
				path: '',
				component: UserAccountComponent,
				data: {
					title: 'Customer-app.body.user.account',
					breadcrumb: 'Cart'
				}
			},
			{
				path: 'order',
				component: OrderComponent,
				data: {
					title: 'Customer-app.body.user.order'
					//breadcrumb: 'Cart'
				}
			},
			{
				path: 'order/:orderId',
				component: OrderDetailComponent,
				data: {
					title: 'Customer-app.body.user.order-detail'
					//breadcrumb: 'Cart'
				}
			},
			{
				path: 'book',
				loadChildren: () =>
					import('./modules/customer-reading-progress/customer-reading-progress.module').then(
						(m) => m.CustomerReadingProgressModule
					)
				//canActivate: [AuthGuard] ,
			},
			{
				path: 'change-password',
				loadChildren: () =>
					import('./modules/customer-change-password/customer-change-password.module').then(
						(m) => m.CustomerChangePasswordModule
					)
				//canActivate: [AuthGuard] ,
			}
		]
	}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class UserRoutingModule {}
