import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Injectable} from '@angular/core';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {OrderResponse} from 'src/app/core/models/customer/order/response/OrderResponse.viewmodel';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {CustomerUrlOrder} from 'src/app/core/utils/URL/customer-url/customer-order/customer-url-order';
import {OrderAndOrderDetailResponse} from 'src/app/core/models/customer/order/response/OrderAndOrderDetailResponse';

@Injectable({
	providedIn: 'root'
})
export class OrderService {
	languageCode = 'vn';
	constructor(private http: HttpClient, private customerUrlOrderString: CustomerUrlOrder) {}

	getOrders(pagingRequestBase: PagingRequestBase): Observable<ApiResult<PagedResult<OrderResponse>>> {
		pagingRequestBase.languageCode = this.languageCode;
		return this.http.get<ApiResult<PagedResult<OrderResponse>>>(
			this.customerUrlOrderString.customerGetOrders(pagingRequestBase)
		);
	}
	getOrderDetail(orderId: number): Observable<ApiResult<OrderAndOrderDetailResponse>> {
		return this.http.get<ApiResult<OrderAndOrderDetailResponse>>(
			this.customerUrlOrderString.getOrderDetails(orderId)
		);
	}
	deleteOrder(orderId: number): Observable<ApiResult<boolean>> {
		return this.http.delete<ApiResult<boolean>>(this.customerUrlOrderString.deleteOrder(orderId));
	}
}
