import {OrderService} from 'src/app/core/services/customer/order/order.service';
import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Observable} from 'rxjs';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {OrderAndOrderDetailResponse} from 'src/app/core/models/seller/order/response/OrderAndOrderDetailResponse';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-order-detail',
	templateUrl: './order-detail.component.html',
	styleUrls: ['./order-detail.component.less']
})
export class OrderDetailComponent implements OnInit {
	isLoading = true; //skelton
	orderId?: number;
	host: string = environment.apiUrl;
	orderDetails$?: Observable<ApiResult<OrderAndOrderDetailResponse>>;
	constructor(public router: Router, private route: ActivatedRoute, private orderService: OrderService) {}

	ngOnInit(): void {
		console.log('this.orderId');
		this.isLoading = true;
		if (this.route.snapshot.paramMap.get('orderId')) {
			this.orderId = +this.route.snapshot.paramMap.get('orderId');

			this.getOrderDetail(this.orderId);
		}
	}
	getOrderDetail(orderId: number) {
		console.log(this.orderId);
		this.orderDetails$ = this.orderService.getOrderDetail(orderId);
		console.log(this.orderDetails$);
	}
}
