import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlOrder extends CustomerUrlBase {
	constructor() {
		super();
	}
	customerGetOrders(pagingRequestBase: PagingRequestBase) {
		return (
			this.customerUrl +
			`/order/orders?pageindex=${pagingRequestBase.pageIndex}&pagesize=${pagingRequestBase.pageSize}&languageCode=${pagingRequestBase.languageCode}`
		);
	}
	getOrderDetails(orderId: number) {
		return this.customerUrl + `/order/order-detail/` + orderId;
	}
	deleteOrder(orderId: number) {
		return this.customerUrl + `/order/order/` + orderId;
	}
}
