import {customerLoginResetAction} from './../../../../../../../core/store/customer/login/customer-login.actions';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {formatDate} from '@angular/common';
import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Store} from '@ngrx/store';
import {takeUntil} from 'rxjs/operators';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {ProfileUpdateRequest} from 'src/app/core/models/customer/customer/request/ProfileUpdateRequest';
import {CustomerInforResponse} from 'src/app/core/models/customer/customer/response/CustomerInforResponse';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {CustomerAccountService} from 'src/app/core/services/auth/customer/customer-account.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {CustomerAppState} from 'src/app/core/store/customer/customer-app.state';
import {customerGetInforAction} from 'src/app/core/store/customer/infor/customer-infor.actions';
import {customerInforCustomerStateSelector} from 'src/app/core/store/customer/infor/customer-infor.selector';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-user-account',
	templateUrl: './user-account.component.html',
	styleUrls: ['./user-account.component.less'],
	providers: [DestroyService]
})
export class UserAccountComponent implements OnInit {
	isLoading = false; //return true: something is loading and false if something is completed
	returnUrl = '';
	customerInfor: CustomerInforResponse; //the personal infor after taking from store
	customerInforForm: FormGroup; //form Group of information fields
	host: string = environment.apiUrl; //host of server
	/**is modified any personal infor in formGroup, return true: any fields changed, return false: fields is similar with init data */
	modified = false;
	thumbshandlePreview: string; //url string of avatar image after uploading to server
	isThumbshandlePreview: boolean; //is url avatar image string null? return false if null => can not submit and true if not null
	isModalVisible: boolean;
	confirmPasswordString = '';
	constructor(
		private formBuilder: FormBuilder,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private accountService: CustomerAccountService,
		private store: Store<CustomerAppState>,
		private notificationService: NotificationService,
		private spinnerService: SpinnerService,
	
	) {
		//init data
		///this.confirmPasswordString = '';
		this.isModalVisible = false;
		this.thumbshandlePreview = '';
		this.customerInfor = {} as CustomerInforResponse;
		this.isThumbshandlePreview = false;
		//init form
		this.customerInforForm = this.formBuilder.group({
			email: [{value: '', disabled: true}, [Validators.required, Validators.minLength(3), Validators.pattern]],
			name: ['', [Validators.required, Validators.minLength(1), Validators.pattern]],
			phoneNumber: ['', [Validators.required, Validators.minLength(3)]],
			dob: ['', [Validators.required, Validators.minLength(3)]],
			country: ['', [Validators.required, Validators.minLength(3)]],
			province: ['', [Validators.required, Validators.minLength(3)]],
			city: ['', [Validators.required, Validators.minLength(3)]],
			address: ['', [Validators.required, Validators.minLength(3)]]
		});
	}

	ngOnInit(): void {
		
		this.getUserInfor(); //get personal infor from store
		this.initFormDataFromStore(); //attach data from store into formGroup
		this.checkFormValueChange(); //check any fields changed
	}
	getUserInfor() {
		this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.store.dispatch(customerGetInforAction());
		this.store
			.select(customerInforCustomerStateSelector)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						this.customerInfor = result;
						console.log(this.customerInfor);
						if (result.image) {
							if (result.image.imagepath) {
								this.thumbshandlePreview = result.image.imagepath;
								this.isThumbshandlePreview = true;
							}
						}
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	initFormDataFromStore() {
		setTimeout(() => {
			this.customerInforForm.setValue({
				email: this.customerInfor.email != null ? this.customerInfor.email : '',
				name: this.customerInfor.firstName != null ? this.customerInfor.firstName : '',
				phoneNumber: this.customerInfor.phoneNumber != null ? this.customerInfor.phoneNumber : '',
				dob: formatDate(this.customerInfor.dob != null ? this.customerInfor.dob : '', 'yyyy-MM-dd', 'en'),
				country: this.customerInfor.country != null ? this.customerInfor.country : '',
				province: this.customerInfor.province != null ? this.customerInfor.province : '',
				city: this.customerInfor.city != null ? this.customerInfor.city : '',
				address: this.customerInfor.address != null ? this.customerInfor.address : ''
			});
			this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
		}, 5000);
	}
	checkFormValueChange() {
		this.customerInforForm.valueChanges.subscribe((result) => {
			console.log('valueChanges');
			console.log(result);
			console.log(this.customerInfor);
			if (
				result.phoneNumber === (this.customerInfor.phoneNumber != null ? this.customerInfor.phoneNumber : '') &&
				result.dob ===
					formatDate(this.customerInfor.dob != null ? this.customerInfor.dob : '', 'yyyy-MM-dd', 'en') &&
				result.country === (this.customerInfor.country != null ? this.customerInfor.country : '') &&
				result.name === (this.customerInfor.firstName != null ? this.customerInfor.firstName : '') &&
				result.province === (this.customerInfor.province != null ? this.customerInfor.province : '') &&
				result.city === (this.customerInfor.city != null ? this.customerInfor.city : '') &&
				result.address === (this.customerInfor.address != null ? this.customerInfor.address : '') &&
				this.thumbshandlePreview ===
					(this.customerInfor.image?.imagepath != null ? this.customerInfor.image?.imagepath : '')
			) {
				this.modified = false;
			} else {
				this.modified = true;
			}
			console.log(this.modified);
		});
	}
	confirmUpdate() {
		this.submitForm();
	}
	submitForm(): void {
		try {
			this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			const val = this.customerInforForm.value;
			const profileUpdate: ProfileUpdateRequest = {
				phoneNumber: val.phoneNumber,
				dob: val.dob,
				name:val.name,
				country: val.country,
				province: val.province,
				city: val.city,
				address: val.address,
				avatarImage: this.thumbshandlePreview
			};
			this.accountService
				.customerUpdateProfile(profileUpdate)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(res) => {
						if (res) {
							if (res.isSuccessed) {
								this.store.dispatch(customerGetInforAction());
								//this.spinnerService.subjectSpinner$.next(false);
								if (res.message) {
									this.notificationService.showSuccessNotification(res.message);
								}
							}
						}
						this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
					}
				);
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	confirmDelete() {
		this.isModalVisible = true;
	}
	confirmDeleteModalOk() {
		this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.isModalVisible = false;
		this.accountService.confirmPassWord({passWord: this.confirmPasswordString}).subscribe(
			(result) => {
				if (result) {
					this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					if (result.isSuccessed) {
						this.store.dispatch(customerLoginResetAction({message: 'logout'}));
						if (result.message) {
							this.notificationService.showSuccessNotification(result.message);
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				}
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
	deleteAccount() {
		console.log('delete');
	}
	updateMesssages(confirmPasswordString: string) {
		//input password and chekc password true or false
		this.confirmPasswordString = confirmPasswordString;
	}
	removeImageFile() {
		this.thumbshandlePreview = '';
		if (this.customerInfor.image) {
			if (
				this.thumbshandlePreview ===
				(this.customerInfor.image.imagepath != null ? this.customerInfor.image?.imagepath : '')
			) {
				this.modified = false;
			} else {
				this.modified = true;
			}
			this.isThumbshandlePreview = false;
		} else {
		}
	}
	cancel(): void {
		this.isModalVisible = false;
		return;
	}

	confirm(): void {
		this.removeImageFile();
		this.checkFormValueChange();
		this.notificationService.showSuccessNotification('Đã Xóa Thành Công');
	}
	beforeUpload = (file: File): boolean => {
		const formData: FormData = new FormData();
		formData.append('productImage', file, file.name);
		this.accountService
			.postAvatarImagePreview(formData)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.resultObj != null) {
						if (result.resultObj.imagePath != null) {
							this.thumbshandlePreview = result.resultObj.imagePath;
							this.isThumbshandlePreview = true;
						}
						console.log(this.thumbshandlePreview);
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
		return false;
	};
}
