import {SpinnerService} from './../../../../../../../core/services/comon-services/spinner/spinner.service';
import {takeUntil} from 'rxjs/operators';
import {Component, OnInit} from '@angular/core';
import {environment} from 'src/environments/environment';
import {OrderResponse} from 'src/app/core/models/customer/order/response/OrderResponse.viewmodel';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {OrderService} from 'src/app/core/services/customer/order/order.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';

@Component({
	selector: 'app-order',
	templateUrl: './order.component.html',
	styleUrls: ['./order.component.less'],
	providers: [DestroyService]
})
export class OrderComponent implements OnInit {
	isLoading = true; //skelton
	isNotHaveData: boolean = false;
	orderResponses: Array<OrderResponse>;
	totalProductQuantity = 10;
	totalProductPrice = 10;
	host: string = environment.apiUrl;
	pageIndex: number = 1;
	constructor(
		private destroy$: DestroyService,
		private orderServie: OrderService,
		private appErrorHandler: AppErrorHandler,
		private noti: NotificationService,
		private spinner: SpinnerService
	) {
		this.orderResponses = [];
	}
	ngOnInit(): void {
		this.getOrders(1);
	}
	getOrders(pageIndex: number) {
		this.orderServie
			.getOrders({pageIndex, pageSize: 10})
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						if (result.isSuccessed) {
							if (result.resultObj) {
								Array.prototype.push.apply(this.orderResponses, result.resultObj.items);
								console.log(this.orderResponses);
								if (this.orderResponses.length < 0) {
									this.isNotHaveData = true;
								}
							}
						} else {
							this.isNotHaveData = true;
						}
					} else {
						this.isNotHaveData = true;
					}
					this.isLoading = false;
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	onScroll() {
		this.pageIndex = this.pageIndex + 1;
		this.getOrders(this.pageIndex);
	}
	deleteOrder(orderId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.orderServie.deleteOrder(orderId).subscribe(
			(result) => {
				if (result) {
					if (result.isSuccessed) {
						if (result.message) {
							this.orderResponses = [];
							this.getOrders(1);
							this.noti.showSuccessNotification(result.message);
						}
					} else {
						if (result.message) {
							this.noti.showErrorNotification(result.message);
						}
					}
				}
				this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
}
